.container {
    padding: 3.5rem;
    background-color: #F8F9FA;
}
  
.container__head {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem !important;
}

.container__title {
    font-size: 3.5rem;
    font-weight: bold;
    text-align: center;
    color: #118AB2;
}
.container__subtitle {
    text-align: center;
    color: #118AB2;
}

.container__body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
  
  .psection__table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .psection__table th, td {
    text-align: center;
    padding: 0.5rem;
    border: 1px groove #64748B;
  }
  
  @media (min-width: 768px) {
    
  }
  
  @media (min-width: 992px) {
    .psection__body {
      flex-direction: row;
      justify-content: space-around;
    }
  }