.container {
    padding: 3.5rem;
    background-color: #F8F9FA;
}
  
.container__head {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem !important;
}

.container__title {
    font-size: 3.5rem;
    font-weight: bold;
    text-align: center;
    color: #118AB2;
}
.container__subtitle {
    text-align: center;
    color: #118AB2;
}

.container__body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@media (min-width: 768px) {
    .container__body {
        flex-direction: column;
        justify-content: space-around;
    }
}
  
@media (min-width: 992px) {
    .container__body {
        flex-direction: row;
        justify-content: space-around;
    }

    .card__container {
        width: 30%;
        margin: 0 0.75rem 3rem;
    }
}